import React from 'react'
import { useSelector } from 'react-redux'
import { NavLink } from 'react-router-dom';

const Navbar = () => {
    const cartCount=useSelector((state)=>state.handleCart).length;
    return (
        <div className='shadow-md w-[100%] bg-white'>
            <div className=' h-20 max-w-[960px] flex justify-between items-center px-2 mx-auto'>
                <NavLink to='/ecommerce'><h1 className='font-bold text-2xl'><span className='text-purple-600'>E</span>com-<span className='text-purple-600'>E</span>xpress</h1></NavLink>
                <div className='flex justify-between items-center gap-8 text-xl mx-8'>
                    <i className="fa-solid fa-user"></i>
                    <NavLink to='/Cart' className='relative'>
                        <div className='top-[-10px] left-[15px] absolute h-[20px] w-[20px] bg-purple-600 text-xs rounded-xl flex justify-center items-center text-[white]'>{cartCount}</div>
                        <i className="fa-solid fa-cart-shopping"></i>
                    </NavLink>
                </div>
            </div>
        </div>
    )
}
export default Navbar