import React from 'react'
import Card from '../Components/Card'
import {Toaster} from 'react-hot-toast'
import SliderCom from '../Components/SliderCom'
const Home = ({Products}) => {
  
  return (
    <>
    <SliderCom/>
    <div className='max-w-[1100px] flex justify-center flex-wrap my-5 mx-auto gap-5'>
      <Toaster position='top-center' reverseOrder={false}/>
      {Products?.map((Data, i) => {
        return (
          <Card id={Data.id} key={i} title={Data.title} price={Data.price} img={Data.images[0]} desc={Data.description}/>
        )
      })}
    </div>
    </>
  )
}

export default Home
