import React,{useState} from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { addItem, delItem } from '../Redux/actions';
import { NavLink } from 'react-router-dom';
const Cart = () => {
  const dispatch = useDispatch();
  const Data = useSelector((state) => state.handleCart);
  const handlePlus = ({ id, title, img, desc, price, qty }) => {
    dispatch(addItem({ id, title, img, desc, price, qty }))
  }
  const handleMinus = ({ id }) => {
    dispatch(delItem({ id }))
  }
  var Total = 0;
  const[promoCode,setPromoCode]=useState('')
  const[discount,setdiscount]=useState(0)

  
  const handlePromo=(e)=>{
    setPromoCode(e.target.value)
    if(e.target.value==='DISC5'){
      setdiscount(5)
    }else if(e.target.value==='DISC10'){
      setdiscount(10)
    }else {
      setdiscount(0)
    }
  }

  return (
    <div className='max-w-[1100px] my-5 mx-auto'>
      {Data.length > 0 ?
        <div className='grid md:grid-cols-4 grid-cols-1 justify-items-center mx-5'>
          <div className='mx-5 w-full col-span-3 h-[600px] bg-white shadow rounded'>
            <div className='my-6 flex justify-between items-center pb-5'>
              <h3 className='md:text-3xl mx-10 font-bold'>Shopping&nbsp;Cart</h3>
              <h3 className='md:text-xl mx-16 font-bold'>{Data.length}&nbsp;Items</h3>
            </div>
            <div className='md:grid hidden my-2 grid-cols-6 gap-5 text-sm py-5 px-3 justify-items-start items-center'>
              <div className="col-span-3 mx-10 font-semibold ">Product Details</div>
              <div className="mx-3 font-semibold">Quantity</div>
              <div className="font-semibold">Price</div>
              <div className="font-semibold">Total</div>
            </div>
            <div className='h-[420px] overflow-auto'>
              {Data.map((D, i) => {
                Total = Total + (D.price * D.qty)
                return (
                  <div className='grid md:grid-cols-6 grid-cols-2 gap-5 py-5 pl-4 justify-items-center items-center border' key={i}>
                    <div className="md:col-span-3 flex gap-2">
                      <img src={D.img} className='rounded md:w-[80px] w-[50px]' alt='img'/>
                      <div className='text-sm flex flex-col justify-center'>
                        <p className=' max-w-[150px] truncate h-[20px]'>{D.title}</p><br />
                        <span className='text-red-500 text-xs cursor-pointer' onClick={() => handleMinus(D)}>Remove</span>
                      </div>
                    </div>
                    <div className="flex justify-start items-start gap-3 mx-5">
                      <button onClick={() => handleMinus(D)} className='text-red-500 font-bold text-xl'>-</button>
                      <div className='border-2 w-8 text-center font-medium text-sm'>{D.qty}</div>
                      <button onClick={() => handlePlus(D)} className='text-green-500 font-bold text-xl'>+</button>
                    </div>
                    <div className="font-medium text-sm">₹ {D.price}</div>
                    <div className="font-medium text-sm">₹ {D.price * D.qty}</div>
                  </div>
                )
              })}
            </div>
          </div>
          <div className='h-[600px] w-full px-5 flex flex-col justify-between shadow rounded' style={{ background: '#F4F4F4' }}>
            <div>
              <h3 className='text-xl font-bold mt-12 border-b pb-5 '>Order Summary</h3>
              <div className='flex justify-between mt-5'>
                <h4 className='font-bold'>ITEMS {Data.length}</h4>
                <h4 className='font-bold'>₹ {Total}</h4>
              </div>
            </div>
            <div className='mt-5'>
              <h5 className='font-medium'>Shipping</h5>
              <input type='text' className='text-sm border-0 w-full outline-none bg-white py-2 px-4 my-4 rounded' value='Standard Delivery - ₹50' readOnly />
            </div>
            <div className=''>
              <h6 className='font-bold text-sm'>PROMO CODE </h6>
              <input type='search' className='text-sm border-0 w-full outline-none bg-white py-2 px-4 mt-4 rounded' value={promoCode} onChange={(e)=>handlePromo(e)} placeholder='Enter your code' />
              {promoCode.length<1?"":discount<1?<span className='text-xs text-red-600'>Promo code not available</span>:<span className='text-xs text-green-600'>Promo code is available</span>}<br/>
              {/*<button className='bg-purple-500 hover:bg-purple-700 text-white font-bold py-2 px-4 mt-4 rounded text-xs' onClick={handleApply}>Apply</button>*/}
            </div>
            <div className='flex justify-between border-t pt-5'>
              <h5 className='font-bold'>Total Cost</h5>
              <h5 className='font-bold text-green-700'>
                ₹ {discount>0?((Total-(Total*discount)/100)+50).toFixed(2):(Total+50).toFixed(2)} &nbsp;
                </h5>
            </div>
            <button className='bg-purple-800 text-white font-bold py-2 px-4 rounded text-xs w-full mb-10'>CHECKOUT</button>
          </div>
        </div>
        : 
        <div className='gap-10 h-[600px] w-full bg-white shadow rounded flex justify-center items-center flex-col'>
          <h5 className='font-semibold text-2xl'>Your cart is empty!</h5>
          <span>Add items to it now.</span>
          <NavLink to='/ecommerce'><button className='bg-purple-500 hover:bg-purple-700 text-white font-bold py-2 px-4 rounded text-xs'>Shop Now</button></NavLink>
        </div>
      }
    </div>
  )
}

export default Cart
