import React, { useState, useEffect } from "react";
import axios from 'axios';
import Slider from "react-slick";
const SliderCom = () => {
    const settings = {
        dots: true,
        className: "center",
        infinite: true,
        slidesToShow: 1,
        swipeToSlide: true,
        arrows: false,
        autoplay: true,
        speed: 1000,
        autoplaySpeed: 6000,
        cssEase: "linear",
        afterChange: function (index) {
            console.log(
                `Slider Changed to: ${index + 1}, background: #222; color: #bada55`
            );
        }
    }
    const APIFT = 'https://09april2000.github.io/fakestoreapi/banner.json';
    const [banner, setbanner] = useState();
    
    const fetch_data= async()=>{
        const response=await axios.get(APIFT);
        setbanner(response.data)
    }
    useEffect(() => {
        fetch_data() 
    }, [])
    
    console.log(banner)
    return (
        <div className="slider-container mb-10">
            <Slider {...settings}>
                {banner?.map((D, i) => {
                    return (
                        <div className="h-[100%] bg-slate-500" key={i}>
                            <img src={D.img} />
                        </div>
                    )
                })}
            </Slider>
        </div>
    )
}

export default SliderCom
