import React, { useRef } from 'react'
import toast from 'react-hot-toast';
import { useDispatch } from 'react-redux';
import { addItem } from '../Redux/actions';
import { motion ,useInView} from 'framer-motion';
import { NavLink } from 'react-router-dom';

const Card = ({ id,title, img, desc, price }) => {
  const dispatch=useDispatch()
  const ref=useRef(null);
  const isInView=useInView(ref,{once:true})
  const qty =0;
  const handleCart = () => {
    toast.success('Item Successfully Added to Cart');
    dispatch(addItem({id,title, img, desc, price,qty}))
  }
  console.log('hhh')
  return (
    <>
      <div className="max-w-[240px] h-[380px] rounded overflow-hidden shadow-lg bg-white" ref={ref} style={{transition:'0.7s ease',marginTop:isInView?'0px':'200px'}}>
        <img className="w-full h-[200px]" src={img} alt="Sunset in the mountains" />
        <div className="px-6 py-4">
          <div className="font-bold text-xl mb-2 truncate">{title}</div>
          <p className="text-gray-700 text-base truncate">
            {desc}
          </p>
          <p className='text-center font-bold py-2'>₹ {price}</p>
        </div>
        <div className="flex justify-between px-4 pb-3">
          <motion.span
            initial={{ scale: 0.9 }}
            whileHover={{ scale: 1 }}
            className="inline-block bg-gray-200 rounded-[5px] px-3 py-2 text-xs font-semibold text-gray-700 mr-2 mb-2 cursor-pointer" onClick={handleCart}> <i className="fa-solid fa-cart-plus text-green-700"></i> Add to Cart</motion.span>
          <NavLink to='/cart' onClick={()=>dispatch(addItem({id,title, img, desc, price,qty}))}><span className="inline-block bg-purple-600 rounded-[5px] px-3 py-2 text-xs font-semibold text-white mr-2 mb-2  cursor-pointer">BUY NOW</span>
          </NavLink>
        </div>
      </div>
    </>
  )
}

export default Card
